@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --navbarH: 104px;
  --linkHoverDark: #118496;
  --linkHoverDarker: #09444d;
  --bgInfo: rgba(23,162,184,1);
  --bgInfoLight: rgba(23,162,184,0.75);
  --bgImgWall: url(data:image/png;base64,);
  --paddingTopSection: calc(var(--navbarH) + 15px);
  --linkHoverLight: rgba(23,162,184,0.2);

}

html {
  scroll-behavior: smooth;
}

.font-raleway {
  font-family: 'Raleway', sans-serif;

}

.headingText {
  font-size: 3rem;
}

.content {
  width: 62.5%;
  margin: 0 auto;
  min-height: calc(110vh - 265px);
}

.section {
  padding-top: calc(104px + 15px);
  padding-top: var(--paddingTopSection);
  background-image: url(data:image/png;base64,);
  background-image: var(--bgImgWall)

}

.sectionContent {
  width: 87%;
  margin: 0 auto;
}

@media screen and (max-width: 576px) {
        
  .content {
    width: 80%;
  }
  .headingText {
      font-size: 2rem;
  }

  .section {
    padding: calc(104px + 15px) 15px 5px;
    padding: calc(var(--navbarH) + 15px) 15px 5px;

  }

  .sectionContent {
    width: 100%;
  }

}
#welcome {
    min-height: 100vh;
    background-image: url(/static/media/welcome.e77b1a2b.jpg);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
  
#welcomeContainer {
    background-color: rgba(255, 255, 255, 0.75);
}

#headshotContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

#headshot {
    max-width: 100%;
} 

@-webkit-keyframes rToL {
    0%   {left:900px; top:0px;}
    100% {left:0px; top:0px;}
} 

@keyframes rToL {
    0%   {left:900px; top:0px;}
    100% {left:0px; top:0px;}
}

#welcomeH1 {
    position: relative;
    font-size: 6rem;
    -webkit-animation-name: rToL;
            animation-name: rToL;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}

.welcomeH2 {
    position: relative;
    font-size: 3rem;
    -webkit-animation-name: rToL;
            animation-name: rToL;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

#aboutBtn {
    position: relative;
    -webkit-animation-name: rToL;
            animation-name: rToL;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
}

@media screen and (max-width: 1000px) {
    
    #welcomeH1 {
        font-size: 5rem;
    }
    .welcomeH2 {
        font-size: 2rem;
    }

}

@media screen and (max-width: 768px) {
    
    #welcomeH1 {
        font-size: 4rem;
    }
    .welcomeH2 {
        font-size: 1.5rem;
    }

}

@media screen and (max-width: 576px) {
    
    #welcomeH1 {
        font-size: 3rem;
    }
    .welcomeH2 {
        font-size: 1rem;
    }
    #welcome {
        padding: 15px;
    }

}



  #headshot2 {
    width: 220px;
    margin: 0px 15px 5px 0px;
  } 

  .aboutText {
    line-height: 32px;
  }

  @media screen and (max-width: 576px) {

    #headshot2 {
        width: 100%;
        margin: 0 auto;
    }  
    
}


.navbar {
    padding: 0 5% 0 0;
}

.navbarText {
    font-size: 2rem;
}

.navLink {
    color: white !important;
}

.navLink:hover {
    color: var(--linkHoverDarker) !important;

}

.navbar-brand {
    max-width: 100%;
}
  
@media screen and (max-width: 992px) {
      
    .navbar {
        padding: 0;
    }

    li.nav-item:nth-child(2n -1) {
        background-color: var(--linkHoverDark);

      }

  }

.pCard {
    height: 100%;
    width: 100%;
    opacity: 0.8;
    border: 3px solid var(--bgInfo);
}

.pCard:hover {
    opacity: 1.0;
}

.projectBtn {
    /* background-color: var(--bgInfoLight); */
    background: linear-gradient(0deg, var(--bgInfo) 0%, rgba(23,162,184,0.5) 100%);
    position: absolute;
    bottom: 0;
    border: 0px;
    border-radius: 0 100px 0px 0;
    text-align: left;
    width: 100%;
}
  
.projectH1 {
    font-size: 1.5rem;
}

.projectH2 {
    font-size: 1rem;
}

#secSmall {
    display: none;
}


